import React, { useEffect, useState } from 'react';
import Structure from '../../Structure';
import ContentBox from './ContentBox';
import styled from 'styled-components';

const Container = styled.div`
    width: 100%;
    height: 100%;
    margin-top: 20px;
    padding: 10px;
`;

const Title = styled.div`
    font-size: 17px;
    font-weight: 600;
    margin-bottom: 20px;
    margin-top: 20px;
`;

export default function HowItWorks() {
    return (
        <Structure howItworkPage>
            <Container>
                <Title>Users</Title>
                <div className="row">
                    <p>
                        With sojiare, eliminate the dilemma of what I ordered and what I got by searching and running a
                        background check on the business, brand, contact number or email before the transaction.{' '}
                    </p>
                    <ContentBox number="1" title="SIGN UP" desc="Sign up to get search results and feedback" />
                    <ContentBox
                        number="2"
                        title="FEEDBACK AND RESULT"
                        desc="Check existing feedback and the results of your search queries"
                    />
                </div>
            </Container>
            <Container>
                <Title>Company</Title>
                <div className="row">
                    <ContentBox number="1" title="SIGN UP" desc="Sign up and register your company with sojiare" />
                    <ContentBox
                        number="2"
                        title="SHARELINK"
                        desc="Post link on social media pages bio for easy access and verification. Encourage existing customers to review and post comments about your company. New customers can run a background check on your business to ascertain credibility"
                    />
                    <ContentBox
                        number="3"
                        title="TRACK FEEDBACK"
                        desc="Enjoy premium access by tracking feedback of comments and reviews posted about your company"
                    />
                </div>
            </Container>
            <Container>
                <Title>Government</Title>
                <p>
                    Government agencies are subject to tax, benefit, phishing and employment fraud. Government agencies
                    can register their domain link, emails, and contact numbers used in communicating with the general
                    public to avoid impersonation. If citizens receive messages from government agencies, they can
                    search and verify the mailing address, phone number and source for authentication. Citizens will get
                    the results of their search queries.
                </p>
            </Container>
        </Structure>
    );
}
