import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
    display: flex;
    margin: 10px;
`;

const FigureBox = styled.div`
    background: rgba(255, 102, 0, 0.06);
    border-radius: 5px;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ff6600;
    margin-right: 15px;
`;

const DescBox = styled.div`
    flex: 1;
    padding-left: 10px;
    padding-right: 10px;
`;

const DescBoxTitle = styled.div`
    font-weight: bold;
    margin-bottom: 5px;
`;

export default function ContentBox({ number, title, desc }) {
    return (
        <Container>
            <FigureBox>{number}</FigureBox>
            <DescBox>
                <DescBoxTitle>{title}</DescBoxTitle>
                <p>{desc}</p>
            </DescBox>
        </Container>
    );
}
